@import '../../variables.scss';
.ant-menu-horizontal > .ant-menu-item, .ant-menu-horizontal > .ant-menu-submenu{
  top:0px;
}
.LowerHeader_Sort{
  .ant-checkbox-inner{
    border-radius: 8px;
  }
}
.cartCount_mobile{
  display: none;
  position: absolute;
  top:-10px;
  right:-10px;
  background:#722e6d;
  font-weight: 500;
  height:24px;
  width: 24px;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 12px;
}
.ant-breadcrumb{
  >span:last-child{
    a{
      text-decoration: none;
    }
  }
  .ant-breadcrumb-link{
    a{
      font-size: 16px;
      font-weight: 400;
      color: #000000;
      text-decoration: underline;
    }
  }
}
.pageTitle{
  font-size: 36px;
  font-weight: 500;
}
.LowerHeader_Sort{
  font-size: 16px;
  font-weight: 400;
  span{
    color: #040404;
  }
  .ant-dropdown-link{
    color: #8e3a88;
  }
}
.CustomSearchInput{
  .ant-input{
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px
  }
  input{
    height: 40px;
    background: rgba(#000000,0.05);
    border:none;
    padding-left: 25px;
  }
  button{
    height:40px;
    width: 40px;
  }
}
.ant-btn-primary{
  border:none;
}
.ant-menu-item {
  color: black;
  i {
    color: black !important;
  }
}
.custom_icon{
  margin-right: 5px;
}

.MainHeader {
  position: sticky;
  left: 0px;
  top: 0px;
  width: 100%;
  z-index: 100;

  color: rgb(255, 255, 255);
  a {
    color: rgb(255, 255, 255);
  }

  .MainHeader_Upper {
    padding: $paddings;
    padding:0 20px 0 70px;
    height:70px;
    .Menu_Item-Text{
      line-height:12px;
      text-align: left;
    }
    .profile_setting_menu{
      margin-right: 10px;
    }
    .Busket_info{
      font-size: 9px;
      line-height: 10px;
    }
    .ant-menu-item{
      margin-right: 10px;
    }
    .MainHeader__ItemHeight {
      height: 100%;
    }
    background-color: $darkerPurple;

    .MainHeader_Logo {
      font-size: 19px;
      font-family: 'Roboto';
      font-weight: bold;
      line-height: 1.5;
      margin-bottom: 0;
      color: rgb(255, 255, 255);
      &.__Huge {
        display: block;
      }

      &.__Small {
        display: none;
      }

      .Logo_Add {
        color: #f5acf0;
      }
    }

    .MainHeader_YourManager {
      font-size: 12px;
      font-family: 'Roboto';
      line-height: 1;
      height: 40px;
      .YouManager_Name {
        border-bottom: 1px dotted rgb(255, 255, 255);
      }
    }

    .MainHeader_Upper_Right {
      border: none !important;
      color: white;
      background-color: transparent;
      text-align: right;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      &.ant-menu-horizontal {
        line-height: 40px;
        top:0;
      }

      .submenu-title-wrapper {
        display: inline-flex;
        align-items: center;
      }

      .ant-menu-item {
        color: white;
        i {
          color: white !important;
        }
      }
      .Menu-Header__Slim {
        display: none;
      }
      li,
      .ant-menu-submenu-title,
      a {
        padding: 0;
        margin-left: 10px;
        height: 100%;
        &:hover {
          background-color: #722e6d;
          color: white;
          svg {
            color: white;
          }
        }
      }

      .ant-menu-submenu-title {
        display: flex;
        align-items: center;
      }

      .ant-menu-item {
        display: inline-flex;
        align-items: center;
        padding: 0 10px;
        & > a {
          display: flex;
          align-items: center;
          margin-left: 0;
          color: white;
        }
      }

      & * {
        border: none !important;
      }
    }
  }
  .MainHeader_Lower {
    background-color: $black;
    padding: $paddings;
    padding-top: 10px;
    padding-bottom: 10px;
    a {
      margin-right: 15px;
    }

    .MainHeader_Lower_Menu {
      padding-left: 25px;
      .ant-menu-horizontal {
        line-height: 10px;
       
      }
      &.ant-menu {
        background-color: transparent;

        line-height: 10px;
        .ant-menu-submenu-title {
          span {
            color: white !important;
          }
        }

        .ant-menu-item {
          color: white !important;
          padding: 0 8px 0 0;
          background-color: black !important;
          padding-right:50px;
          font-size: 16px;
          font-weight: 400;
        }
      }
      &,
      * {
        border: none !important;
      }
    }
  }
}

@media all and (max-width: 1532px) {
  .MainHeader {
    .MainHeader_Upper {
      .MainHeader_Upper_Right {
        &.ant-menu-horizontal {
          height: 70px;
        }
      }
    }
  }
}

@media all and (max-width: 992px) {
  .MainHeader {
    .MainHeader_Upper {
      .MainHeader_Logo {
        &.__Huge {
          display: none;
        }

        &.__Small {
          display: block;
        }
      }
    }
  }
}

@media all and (max-width: 768px) {
  .MainHeader .MainHeader_Upper .MainHeader_Upper_Right li, .MainHeader .MainHeader_Upper .MainHeader_Upper_Right .ant-menu-submenu-title, .MainHeader .MainHeader_Upper .MainHeader_Upper_Right a{
    margin-left:0;
  }
  .BuiltIn{
    .Filter-Container{
      margin-bottom: 40px;
    }
  }
  .MainHeader {
    // .MainHeader_Upper {
    //   padding: 0 10px;
    // }
    .MainHeader_Upper {
      padding:0;
      .__Busket{
        .title{
          position: relative;
          .cartCount_mobile{
            display: flex;
          }
          span{
            display:none;
          }
        }
      }
      .Busket_info{
        display: none;
      }
      .MainHeader_Upper_Right {
        &.ant-menu-horizontal {
          height: 70px;
        }
      }
    }
    .Menu_Item-Text {
      display: none;
      &.__Busket {
        display: flex !important;
        .title {
          text-align: left !important;
        }
        .subtitle {
          font-size: 9px;
        }
      }
    }
    .Menu-Header__Slim {
      display: inline;
    }
    .MainHeader_Upper {
      .MainHeader_Logo {
        &.__Huge {
          display: none;
        }

        &.__Small {
          display: block;
        }
      }
    }
  }
}
